:root {
  --background: #ffffff;
  --foreground: #171717;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
}

html {
  /* height: 100%; */
  /* overflow-y: scroll; */
}
body {
  box-sizing: border-box;
  /* min-height: 100%; */
  /* overflow-y: scroll; */
}

/* Общий стиль скроллбара */
::-webkit-scrollbar {
  width: 8px; /* Ширина скроллбара */
}

/* Дорожка (фон скроллбара) */
::-webkit-scrollbar-track {
  background: #121212; /* Тёмный фон */
  border-radius: 10px;
}

/* Ползунок (сам скролл) */
::-webkit-scrollbar-thumb {
  background: linear-gradient(
    180deg,
    #3f3f40,
    #bfbbc4
  ); /* Градиентный скролл */
  border-radius: 10px;
}

/* При наведении на скролл */
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #8e2de2, #5f29b7); /* Обратный градиент */
}

/* Скролл для Firefox */
* {
  scrollbar-width: thin; /* Тонкий скролл */
  scrollbar-color: #403f40 #121212; /* Цвет ползунка и фона */
  box-sizing: border-box;
}

